{#each field as f, i}
  <div class="trout" number={f} on:click={_ => troutClick(i)}></div>
  {#if (i + 1) % 8 == 0}<br>{/if}
{/each}


<script lang="ts">import 'smicle-util';
import * as _util from 'smicle-util';
import { paintTheTrout } from '../ts/PaintTheTrout';
import { createEventDispatcher } from 'svelte';
var dispatch = createEventDispatcher();
export var count;
export var judge;
export var field;
var troutClick = function (i) {
    if (field[i] === 0) {
        colorTheSquares(i);
        count++;
        endConfirmation();
        updateChild();
    }
    else if (field[i] === 23) {
        var queen = _util.range(64).filter(function (n) { return field[n] == 23; })._delete(i);
        resetBoard();
        queen.forEach(colorTheSquares);
        count--;
        endConfirmation();
        updateChild();
    }
};
var colorTheSquares = function (i) {
    changeToKaoriColor(i);
    paintTheTrout(i)
        .filter(function (n) { return field[n] === 0; })
        .forEach(changeToKaedeColor);
};
var resetBoard = function () { return _util.range(64).forEach(function (i) { return field[i] = 0; }); };
var changeToKaedeColor = function (i) { return field[i] = field[i] === 0 ? 25 : field[i]; };
var changeToKaoriColor = function (i) { return field[i] = field[i] === 0 ? 23 : field[i]; };
var endConfirmation = function () {
    if (field.some(function (n) { return n === 0; })) {
        judge = "";
    }
    else if (count === 8) {
        judge = count + "\u500B\u7F6E\u3051\u305F\u306D\u304A\u3081\u3067\u3068\u3046";
    }
    else {
        judge = count + "\u500B\u3057\u304B\u7F6E\u3051\u3066\u306A\u3044\u305E";
    }
};
var updateChild = function () {
    dispatch('updateChild', {
        count: count,
        judge: judge,
        field: field,
    });
};
</script>


<style>
  .trout {
    position: relative;
    width: 50px;
    height: 50px;
    left: -50%;
    float: left;
    border: solid 1px black;
  }

  .trout[number='25'] {
    /* Takagaki Kaede Color */
    background-color: #33d5ac;
  }

  .trout[number='23'] {
    /* Sakuramori Kaori Color */
    background-color: #274079;
  }
</style>
